.users {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: stretch;
  gap: 1em;
  padding: 1em;
}

.user {
  display: grid;
  grid-template-columns: 100px 1fr;
  grid-gap: 5%;
}

.info {
  border: 1px solid #919191;
  padding: 10px;
  border-radius: 20px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 1em;
}

.img__wrap {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
}

.img__wrap img {
  width: 100%;
  display: block;
  border-radius: 50%;
}

.button {
  color: #fff;
  background: green;
  border-radius: 10px;
  padding: 10px;
  border: none;
  width: 100%;
  display: inline-block;
  text-align: center;
}
.button:disabled {
  opacity: 0.7;
}

.text {
  width: fit-content;
}

.location {
  flex-shrink: 0;
  width: 20%;
  font-size: 18px;
  font-weight: 600;
  text-align: end;
  white-space: pre-line;
}

.name {
  font-size: 20px;
  margin-bottom: 2em;
}

.status {
  font-size: 14px;
}
