.pagePagination {
  max-width: 100%;
  display: flex;
  flex-wrap: wrap;
  gap: 0.4em;
  align-items: center;
  justify-content: center;
  position: relative;
  padding: 20px 120px;
}

.nav {
  position: absolute;
}

.navPrev {
  left: 0;
}

.navNext {
  right: 0;
}

.page {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
  cursor: pointer;
  line-height: 1;
  font-size: 12px;
}

.selectedPage {
  font-weight: 700;
}